<template>
  <div>
    <!-- <div id="selector"> -->
    <van-sticky>
      <van-dropdown-menu>
        <van-dropdown-item v-model="current" :options="options" v-on:change="onChange" />
      </van-dropdown-menu>
    </van-sticky>
    <!-- </div> -->
    <div id="list">
      <Card v-for="(d, i) in list" :key="i" :data="d" :ref="'card-'+i" />
    </div>
  </div>
</template>

<script>
  import { data } from '../data'
  import Card from './Card.vue'

  export default {
    components: {
      Card
    },
    data() {
      return {
        list: data.list,
        current: 0,
        options: data.list.map((d, i) => { return { text: d.name, value: i }})
      };
    },
    methods: {
      onChange(index) {
        const cardEle = this.$refs['card-' + index][0].$el;
        // cardEle.scrollIntoView({
        //   alignToTop: true,
        //   behavior: 'smooth'
        // });
        window.scrollTo({
          top: cardEle.offsetTop - 60,
          behavior: 'smooth'
        })
        // window.scrollTo(0, cardEle.offsetTop + 60)
      }
    }
  };
</script>

<style>
  #selector {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 999;
  }
  #list {
    margin: 10px;
  }
</style>